@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptmono.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptmonobold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/Robotocondensed.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Robotocondensedbold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

ul {
  list-style-type: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

details > summary {
  list-style: none;
}
details > summary::-webkit-details-marker {
  display: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.for-tablet {
  display: inline-block;
  word-spacing: -2px;
}
@media (max-width: 767px) {
  .for-tablet {
    display: none;
  }
}

.for-mobile {
  display: none;
}
@media (max-width: 767px) {
  .for-mobile {
    display: block;
  }
}

.for-responsive {
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .for-responsive {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .for-responsive {
    padding: 0;
  }
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    opacity: 0.8;
  }
}

.btn-middle,
.btn-wide {
  color: #f2f5f6;
  cursor: pointer;
  display: flex;
  justify-content: center;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  user-select: none;
}
.btn-middle:disabled,
.btn-wide:disabled {
  opacity: 0.5;
  pointer-events: none;
}
.btn-middle:hover, .btn-middle:focus, .btn-middle:focus-within,
.btn-wide:hover,
.btn-wide:focus,
.btn-wide:focus-within {
  outline: 2px solid #026593;
  outline-offset: 0.3em;
}

.btn-middle {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 5%;
  padding: 10px 15px;
  border: 1px solid #f2f5f6;
  border-radius: 3px;
}
.btn-middle:focus {
  outline: 1px solid #f2f5f6;
}

.btn-wide {
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 1px;
  text-align: center;
  padding: 15px 25px;
}
@media (max-width: 767px) {
  .btn-wide {
    padding: 12px 25px;
  }
}

.modal {
  color: #f2f5f6;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: fixed;
  inset: 0;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 146px 40px 84px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  cursor: pointer;
}
.modal__block {
  width: 500px;
  background-color: #00b2ff;
  border-radius: 3px;
  padding: 40px;
  position: fixed;
  inset: 146px 16px auto 0;
  margin: auto;
  height: fit-content;
}
.modal__content {
  position: relative;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__content h3 {
  font-weight: 400;
  font-size: 30px;
  line-height: 42px;
  margin: 0;
}
.modal__content p {
  font-family: "PT Mono", "Courier", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 40px;
}
.modal__description {
  margin-top: 0;
}
.modal form {
  grid-template-columns: 1fr;
}
.modal form p {
  display: inherit;
  margin: 0;
}
.modal form button {
  width: 100%;
  margin: 4px 0 5px;
}
.modal form input,
.modal form textarea {
  color: #ffffff;
  background-color: #019bdf;
  border: none;
  border-radius: 3px;
  font-family: "PT Mono", "Courier", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  padding: 15px;
}
.modal form input::placeholder,
.modal form textarea::placeholder {
  color: #ffffff;
}
.modal form textarea {
  height: 155px;
  padding-top: 23px;
}
@media (max-width: 767px) {
  .modal form textarea {
    height: 180px;
  }
}
.modal__close-btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 15px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
  transform: rotate(45deg);
}
.modal__close-btn::before, .modal__close-btn::after {
  content: "";
  position: absolute;
  width: 27px;
  height: 2px;
  background-color: #ffffff;
}
.modal__close-btn::before {
  transform: rotate(90deg);
}
.modal__close-btn:active::before, .modal__close-btn:active::after {
  background-color: #ffffff;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal__form-btn {
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  background-color: #041827;
  border: none;
  border-radius: 3px;
  padding: 13px 25px;
  order: 1;
}

.modal__checkbox {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-left: 9px;
  order: 2;
}
@media (max-width: 1023px) {
  .modal__checkbox {
    margin-left: 0;
    margin-bottom: 5px;
  }
}
.modal__checkbox label {
  position: relative;
  display: flex;
  align-items: center;
}
.modal__checkbox input[type=checkbox] + span {
  width: 15px;
  height: 15px;
  margin-right: 9px;
  border: 1px solid #ffffff;
  border-radius: 1px;
}
.modal__checkbox input[type=checkbox] + span:hover, .modal__checkbox input[type=checkbox] + span:focus, .modal__checkbox input[type=checkbox] + span:focus-within {
  outline: 2px solid #ffffff;
  outline-offset: 0.3em;
}
.modal__checkbox input[type=checkbox]:checked + span {
  content: url("../img/svg/icon-tick.svg");
}
.modal__checkbox input[type=checkbox]:focus + span {
  outline: 2px solid #ffffff;
  outline-offset: 0.3em;
}
.modal__checkbox input[type=checkbox]:disabled + span,
.modal__checkbox input[type=checkbox]:disabled ~ small {
  opacity: 0.5;
}
.modal__checkbox small {
  font-family: "PT Mono", "Courier", monospace;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}

.header {
  color: #f2f5f6;
